import { expect, assert } from 'chai';
import * as React from 'react';
import { mount, ReactWrapper } from 'enzyme';

import MobilePreview, {
  IMobilePreviewProps,
  IMobilePreviewState,
  DeviceType,
  IDeviceList
} from '../../mobilePreview/MobilePreview';
import MobilePreviewClickStop, {
  IMobilePreviewClickStopProps
} from '../../mobilePreviewClickStop/MobilePreviewClickStop';
import MobilePreviewRotate from '../../mobilePreviewRotate/MobilePreviewRotate';

describe('Testing MobilePreview click stops', () => {
  let mobilePreview: ReactWrapper<IMobilePreviewProps, IMobilePreviewState>;
  /* tslint:disable:no-any */
  let clickStops: ReactWrapper<IMobilePreviewClickStopProps, any>;
  /* tslint:enable:no-any */
  const deviceList: IDeviceList = {
    phones: [
      { name: 'Phone 1', width: 100, height: 100 },
      { name: 'Phone 2', width: 200, height: 300 },
      { name: 'Phone 3', width: 50, height: 50 }
    ],
    tablets: [
      { name: 'Tablet 1', width: 300, height: 400 },
      { name: 'Tablet 2', width: 400, height: 300 },
      { name: 'Tablet 3', width: 200, height: 200 }
    ],
    desktops: []
  };

  beforeEach(() => {
    mobilePreview = mount<IMobilePreviewProps, IMobilePreviewState>(
      React.createElement(MobilePreview, {
        deviceType: DeviceType.Phone,
        devices: deviceList
      })
    );

    clickStops = mobilePreview.find(MobilePreviewClickStop);
  });

  afterEach(() => {
    mobilePreview.unmount();
  });

  it('creates the correct number of click stops', () => {
    assert.equal(clickStops.length, 3);
  });

  it('has correct name and width', () => {
    assert.equal(clickStops.first().prop('device').name, 'Phone 2');
    expect(clickStops.first().html()).to.match(/width: ?200/i);
  });

  it('reflects selected when clicked', () => {
    assert.equal(clickStops.first().prop('selected'), false);
    clickStops.first().simulate('click');
    assert.equal(clickStops.first().prop('selected'), true);
  });

  it('relfects that only one click stop is selected', () => {
    assert.equal(clickStops.last().prop('selected'), true);
    assert.equal(clickStops.at(1).prop('selected'), false);
    assert.equal(clickStops.first().prop('selected'), false);
  });

  it('reflects that a device is still selected after a rotation', () => {
    assert.equal(clickStops.last().prop('selected'), true);
    mobilePreview.find(MobilePreviewRotate);
    assert.equal(clickStops.last().prop('selected'), true);
  });
});